@import '../../assets/variables.scss';

.ProjectCard {

    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    cursor: pointer;

    text-decoration: none;

    >img {
        width: 100%;
        height: auto;

        border-radius: 8px;

        margin-bottom: 16px;
    }

    >h4 {
        font-family: $font-title;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 110%;

        color: $color-black;
        width: 100%;
        margin-bottom: 16px;
    }

    >p {
        font-family: $font-text;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 140%;

        color: $color-black;
        width: 100%;
        margin-bottom: 16px;
    }

    &-roles {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: auto;
        height: auto;

        gap: 8px;
    }
}